/* eslint-disable react/prop-types */
import React from 'react';
import { Accordion, AccordionDetails, AccordionSummary, Card, Divider, Stack, Typography } from "@mui/material";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function UserCard({ user, totalPoints, scholarship }) {
    const MiniCard = ({ children }) => {
        return (
            <Card>
                <Stack px={2} py={1}>
                    {children}
                </Stack>
            </Card>
        )
    }
    return (
        <>
            <Card variant="outlined">
                <Stack p={3} spacing={1}>
                    <Typography variant='h5' sx={{ fontWeight: 'bold' }}>{user.id}</Typography>
                    <Divider />
                    <MiniCard>
                        <Typography>(5) {user.attendance} : {5 * user.attendance}</Typography>
                    </MiniCard>
                    <Accordion>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="panel1a-content"
                            id="panel1a-header"
                        >
                            <Typography>(2) {user.login.length} : {2 * user.login.length}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            {user.login.map((el) => <Typography key={el}>{el}</Typography>)}
                        </AccordionDetails>
                    </Accordion>
                    <MiniCard>
                        <Typography> : {user.event}</Typography>
                    </MiniCard>
                    <Divider />
                    <Typography variant='h6'> : {user.total}</Typography>
                    <Typography variant='h6'>  : {(user.total/totalPoints*100).toFixed(2)}%</Typography>
                    <Typography variant='h6'>   : {(user.total/totalPoints*scholarship).toFixed(0)}</Typography>
                </Stack>
            </Card>
        </>
    );
}